<template>
  <div id="shopping-cart">
    <!-- 导航栏 -->
    <van-nav-bar
      class="cart-navbar"
      title="订单"
      left-text="返回"
      right-text="删除"
      :fixed="true"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />

    <!-- 全选 -->
    <van-row class="select-all-model">
      <van-col span="2" class="select-all-checkbox">
        <van-checkbox v-model="checked" @click="checkAll" />
      </van-col>
      <van-col span="22" class="select-all-tip">
        <span>全选</span>
      </van-col>
    </van-row>

    <!-- 商品列表 -->
    <van-list>
      <van-checkbox-group v-model="result" ref="checkboxGroup">
        <div v-for="(item, index) in list" :key="index" class="list-item van-hairline--top">
          <van-row class="list-item-row">
            <van-col span="2" class="list-item-checkbox">
              <van-checkbox
                :name="index"
              />
            </van-col>
            <van-col span="22" class="list-item-cart">
              <van-card
                :price="item.price"
                :title="item.title"
                :thumb="item.src">
                  <div slot="num">
                    <van-stepper :name="index" :value="item.count" @change="countChange" />
                  </div>
              </van-card>
            </van-col>
          </van-row>
        </div>
      </van-checkbox-group>
    </van-list>

    <!-- 说明 -->
    <div style="background: #f7f8fa;">
      <van-cell-group class="more-detail">
        <van-field
          v-model="name"
          rows="1"
          label="发货人"
          placeholder="请输入姓名"
        />
      </van-cell-group>
      <van-cell-group class="more-detail">
        <van-field
          v-model="address"
          rows="4"
          autosize
          type="textarea"
          label="收货地址"
          placeholder="请输入地址"
        />
      </van-cell-group>

      <van-button class="more-detail-btn" type="primary" size="large" @click="copy">复制订单</van-button>
    </div>
  </div>
</template>

<script>

import ClipboardJS from 'clipboard'

export default {
  name: 'shoppingCart',
  data () {
    let listData = [
      {
        src: 'http://daji.runcon.cn/_upload/file/2019/07/2019070916284566272878.jpg',
        price: '1',
        title: '高丽雅娜 肉毒去纹眼霜 送按摩仪 【授】【一般贸易】',
        count: 1
      },
      {
        src: 'http://daji.runcon.cn/_upload/file/2019/07/2019070916293813669308.jpg',
        price: '2',
        title: '高丽雅娜 SHO 7天涂抹式肉毒杆菌安瓶精华 【授】【一般贸易】',
        count: 1
      },
      {
        src: 'http://daji.runcon.cn/_upload/file/2019/06/2019061815140404520333.jpg',
        price: '3',
        title: '香蒲丽海洋水光眼膜 60片/盒 绿色 【授】【一般贸易】',
        count: 1
      },
      {
        src: 'http://daji.runcon.cn/_upload/file/2019/07/2019070916304395167195.jpg',
        price: '4',
        title: '香蒲丽 黄金水光面膜凝胶 金色补水淡斑排毒 新版防伪',
        count: 1
      },
      {
        src: 'http://daji.runcon.cn/_upload/file/2019/07/2019070916314008269196.jpg',
        price: '5',
        title: '香蒲丽 绿色眼膜',
        count: 1
      },
      {
        src: 'http://daji.runcon.cn/_upload/file/2019/09/2019092214171071182246.jpg',
        price: '6',
        title: '香港科士威浪漫身体乳润肤乳 200ml 【一般贸易】',
        count: 1
      },
      {
        src: 'http://daji.runcon.cn/_upload/file/2019/07/2019072917344185429279.jpg',
        price: '7',
        title: '飞利浦 5060 剃须刀',
        count: 1
      }
    ]
    return {
      list: listData,
      checked: false,
      result: [],
      name: '',
      address: ''
    }
  },
  methods: {
    onClickLeft () {
      this.$router.push('/')
    },
    onClickRight () {
      if (this.result.length === 0) {
        this.$toast('请选择删除商品')
        return
      }
      this.result.sort()
      for (let i = this.result.length - 1; i >= 0; i--) {
        this.list.splice(this.result[i], 1)
      }
      this.$refs.checkboxGroup.toggleAll(false)
    },
    checkAll () {
      this.$refs.checkboxGroup.toggleAll(!this.checked)
    },
    countChange (value, detail) {
      this.list[detail.name]['count'] = value
    },
    copy () {
      /* eslint-disable no-new */
      new ClipboardJS('.more-detail-btn', {
        text: (trigger) => {
          let text = ''
          for (let index in this.list) {
            text += this.list[index].title + '\t\t' + this.list[index].count + '\n'
          }
          text += '收货人：' + this.name + '\n'
          text += '收货地址：' + this.address
          return text
        }
      })
    }
  }
}
</script>

<style lang="less">
#shopping-cart {
  font-size: 14px;
  height: 100%;
  overflow: auto;
  .cart-navbar {
    background: #6e7bd8;
    .van-nav-bar__title,
    .van-nav-bar__left i, .van-nav-bar__left .van-nav-bar__text,
    .van-nav-bar__right .van-nav-bar__text {
      color: white;
    }
    .van-nav-bar__left .van-nav-bar__text:active,
    .van-nav-bar__right .van-nav-bar__text:active {
      background-color: #21212129;
    }
  }

  .select-all-model {
    background: white;
    height: 34px;
    margin-top: 44px;
    .select-all-checkbox {
      height: 100%;
      .van-checkbox {
        height: 100%;
        position: relative;
        .van-checkbox__icon {
          position: absolute;
          right: 0;
        }
      }
    }
    .select-all-tip {
      padding-left: 14px;
      line-height: 36px;
      text-align: left;
    }
  }

  .list-item {
    background: white;
    .list-item-checkbox {
      height: 86.4px;
      margin: 7.7px 0;
      .van-checkbox {
        height: 100%;
        position: relative;
        .van-checkbox__icon {
          position: absolute;
          right: 0;
        }
      }
    }
    .list-item-cart { }
    .van-card {
      background: white;
      text-align: left;
      .van-card__bottom {
          position: absolute;
          bottom: 0;
          width: 100%;
      }
    }
  }

  .more-detail {
    margin-top: 15px;
  }
  .more-detail-btn {
    width: 90%;
    margin-top: 15px;
    margin-bottom: 50px;
  }
}
</style>
